import { useState } from "react";
import shortid from "shortid";
import s from "./Form.module.css";

export default function Form({ submit }) {
  const [name, setName] = useState("");
  const [number, setNumber] = useState("");

  const formsubmit = (e) => {
    const id = shortid.generate();
    e.preventDefault();
    reset();
    submit({ name: name, number: number, id: id });
  };

  const reset = () => {
    setName("");
    setNumber("");
  };

  const handleInputChange = (e) => {
    const { name, value } = e.currentTarget;

    switch (name) {
      case "name":
        setName(value);
        break;
      case "number":
        setNumber(value);
        break;
      default:
        return;
    }
  };

  return (
    <form onSubmit={formsubmit} className={s.form}>
      <label className={s.label}>
        Name
        <input
          type="text"
          name="name"
          className={s.input}
          value={name}
          onChange={handleInputChange}
          pattern="^[a-zA-Z–∞-—è–ê-–Ø]+(([' -][a-zA-Z–∞-—è–ê-–Ø ])?[a-zA-Z–∞-—è–ê-–Ø]*)*$"
          title="–ò–º—è –º–æ–∂–µ—Ç —Å–æ—Å—Ç–æ—è—Ç—å —Ç–æ–ª—å–∫–æ –∏–∑ –±—É–∫–≤, –∞–ø–æ—Å—Ç—Ä–æ—Ñ–∞, —Ç–∏—Ä–µ –∏ –ø—Ä–æ–±–µ–ª–æ–≤. –ù–∞–ø—Ä–∏–º–µ—Ä Adrian, Jacob Mercer, Charles de Batz de Castelmore d'Artagnan –∏ —Ç. –ø."
          required
        />
      </label>
      <label className={s.label}>
        Phone
        <input
          type="tel"
          name="number"
          className={s.input}
          value={number}
          onChange={handleInputChange}
          pattern="\+?\d{1,4}?[-.\s]?\(?\d{1,3}?\)?[-.\s]?\d{1,4}[-.\s]?\d{1,4}[-.\s]?\d{1,9}"
          title="–ù–æ–º–µ—Ä —Ç–µ–ª–µ—Ñ–æ–Ω–∞ –¥–æ–ª–∂–µ–Ω —Å–æ—Å—Ç–æ—è—Ç—å —Ü–∏—Ñ—Ä –∏ –º–æ–∂–µ—Ç —Å–æ–¥–µ—Ä–∂–∞—Ç—å –ø—Ä–æ–±–µ–ª—ã, —Ç–∏—Ä–µ, –∫—Ä—É–≥–ª—ã–µ —Å–∫–æ–±–∫–∏ –∏ –º–æ–∂–µ—Ç –Ω–∞—á–∏–Ω–∞—Ç—å—Å—è —Å +"
          required
        />
      </label>
      <button type="submit" className={s.button}>
        Submit
      </button>
    </form>
  );
}
